.preview {
  text-align: center;
  align-items: center;
  max-width: 480px;
  justify-content: center;
}

.Login {
  padding-top: 100px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
}

.registration-cssave {
  padding: 30px 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 360px;
}

.registration-cssave form {
  max-width: 800px;
  padding-top: 15px;
  padding-left: 50px;
  padding-right: 50px;
  border-radius: 10px;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.2);
  background-color: #fff;
}

.registration-cssave form h3 {
  font-weight: bold;
  margin-bottom: 30px;
}

.registration-cssave .item {
  border-radius: 10px;
  margin-bottom: 25px;
  padding: 10px 20px;
}

.registration-cssave .create-account {
  text-align: center;
  align-items: center;
  border-radius: 30px;
  padding: 10px 60px;
  font-size: 18px;
  font-weight: bold;
  background-color: #0098c8;
  border: none;
  color: white;
  margin-top: 20px;
  max-width: 220px;
}

.form-control-item {
  border: #f0f0f0 1.5px solid;
  border-radius: 5px;
  padding: 10px;
}

.text-center {
  font-size: 23px;
}

.form-group {
  padding-top: 30px;
}

.check {
  margin-bottom: 5px;
  color: rgb(145, 0, 0);
  font-size: 15px;
  font-weight: 500;
}

.log-in a {
  text-decoration: none;
  color: #0098c8;
  font-weight: 500;
}

.log-in {
  color: black;
  font-size: 14px;
}

.form-control-item {
  border: #f0f0f0 1.5px solid; /* Исходный цвет рамки */
  border-radius: 4px;
  padding: 10px;
  outline: none;
  transition: border-color 0.3s ease; /* Плавная анимация смены цвета */
}

.form-control-item:focus {
  border-color: #0098c8; /* Цвет рамки при фокусе */
}
